




























import { Component, Vue, Prop } from 'vue-property-decorator'
import { Overlay } from 'vant'

@Component({
  components: {
    Overlay,
  },
})
export default class ADialog extends Vue {
  private show: Boolean = false
  private tip: String = ''
  private title: String = ''
  private type: String = ''
  @Prop({ type: Number, default: 120 }) readonly width!: Number
  @Prop({ type: Number, default: 120 }) readonly height!: Number

  private showDialog(title: String, tip: String, type: String = '') {
    this.show = true
    console.log(tip)
    this.title = title
    this.tip = tip
    this.type = type
  }
  private hideDialog() {
    this.show = false
  }
  get getThemeColor() {
    return this.$store.state.themeColor
  }
  private onSure() {
    if (this.type == '') {
      this.hideDialog()
      return
    }
    this.$emit('onSure', this.type)
  }
}
